.iconrun {
  fill: var(--green);
  stroke: var(--green);
}
.iconrun:hover {
  fill: var(--light-green);
  stroke: var(--light-green);
}
.iconrun:active {
  fill: var(--dark-green);
  stroke: var(--dark-green);
}



